<template>
<div class="col-xs-12 col-md-12" v-if="ready">
    <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleSubmit('form1')" >
    <div class="card">
        <div class="card-header">
            <div style='display:flex;flex-direction:row'>
                <strong>{{ header_text }} - {{ proc_id }}</strong>
            </div>
        </div>
    <div class="card-body card-block" style='padding:0 0 0 0'>
    <!--entry point-->
        <div v-if="entry==0">
            <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                    <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                        <div class='ret_wrapper b_view'  @click="select_cat(1)">                       
                            <div class='inner'>
                                <div class='flex' style='position: relative;'>
                                    <div style='position: relative;'>
                                        <div class='hexagon b_view_first'></div>
                                        <div class='b_view_first_char' style='top:-1.2rem;left:2rem'>D</div>
                                    </div>
                                    <div class='hex_subtext'>eclaration Form</div>
                                    <div class='hex_legen'>Select Participant</div>
                                </div>                         
                            </div>
                        </div> 
                    </div>             
                </div>
                <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                    <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                        <div class='ret_wrapper b2'  @click="select_cat(2)">                       
                            <div class='inner'>
                                <div class='flex' style='position: relative;'>
                                    <div style='position: relative;'>
                                        <div class='hexagon b2_first'></div>
                                        <div class='b2_first_char' style='left:2.75rem;top:-0.5rem'>S</div>
                                    </div>
                                    <div class='hex_subtext'>core Sheet</div>
                                    <div class='hex_legen'>Create Categories</div>
                                </div>                         
                            </div>
                        </div> 
                    </div>             
                </div>
            <!--
            <div class="col-12" style='text-align:center;padding-top:5%'>
                <br/>
                    <h1 style='font-size:3em'><u><b>Select Setup Category</b></u></h1><br/><br/><br/>
                    <div class='proc_entry_card tender' @click="select_cat(1)">
                        Tender Declaration
                    </div><br/><br/><br/>
                    <div class='proc_entry_card tender' @click="select_cat(2)">
                        Score Sheet
                    </div><br/><br/><br/>
            </div>-->
        </div>
        <div class="inner_card_bd" v-if="entry==1">
           <!--attendance list-->
           <h3>Select Participant for tender meeting id: {{ display_tag }} </h3><br/><br/>
            <div class="row form-group">
                <div class="col-4"><label class="form-control-label">IMC Manager</label></div>
                <div class="col-6">
                    <select class="form-control select" v-model="imc">
                        <option v-for="s in imc_list" :value="s.name">{{ s.name }}</option>
                    </select>
                </div>
            </div>
            <div class="row form-group">
                <div class="col-4"><label class="form-control-label">Principal (Secondary)</label></div>
                <div class="col-6">
                    <input type="text" class="form-control" placeholder="Full name of Principal" v-model="principal" readonly/>
                </div>
            </div>
            <div class="row form-group" v-if="isSSB">
                <div class="col-4"><label class="form-control-label">Principal (Primany)</label></div>
                <div class="col-6">
                    <input type="text" class="form-control" placeholder="Full name of Principal (Primany)" v-model="principal_p"/>
                </div>
            </div>
            <div class="row form-group" v-if="isSSB">
                <div class="col-4"><label class="form-control-label">Principal (Kindergarden)</label></div>
                <div class="col-6">
                    <input type="text" class="form-control" placeholder="Full name of Principal (Kindergarden)" v-model="principal_k"/>
                </div>
            </div>
            <div class="row form-group" v-if="!isSSB">
                <div class="col-4"><label class="form-control-label">PTA Representative</label></div>
                <div class="col-6">
                    <select class="form-control select" v-model="pta">
                        <option v-for="s in pta_list" :value="s.name">{{ s.name }}</option>
                    </select>
                </div>
            </div>
            <div class="row form-group">
                <div class="col-4"><label class="form-control-label">SGM</label></div>
                <div class="col-6">
                    <select class="form-control select" v-model="sgm">
                        <option v-for="s in sgm_list" :value="s.username">{{ s.name }}</option>
                    </select>
                </div>
            </div>
            <!--Change to live search-->
            <div class="row form-group">
                <div class="col-4">
                     <select class="form-control select" v-model="user_type[0]">
                        <option value="teacher">Teacher</option>
                        <option value="staff">Staff</option>
                    </select>
                </div>
                <div class="col-6">
                    <input type="text" class="form-control" v-model="teacher[0]" @keyup="search_user(0)"/>
                </div>
                <div class="col-2">
                    <button class="form-control button" type="button" @click="add">Add Teacher</button>
                </div>
            </div>
            <div class="row form-group" v-for="n in counter_teacher">
                <div class="col-4">
                    <select class="form-control select" v-model="user_type[n]">
                        <option value="teacher">Teacher</option>
                        <option value="staff">Staff</option>
                    </select>
                </div>
                <div class="col-6">
                    <input type="text" class="form-control" v-model="teacher[n]" @keyup="search_user(n)"/>
                </div>
                <div class="col-2" v-if="n>1">
                    <button class="form-control button" type="button" @click="remove">Remove</button>
                </div>
            </div>
            <!--[END] Change to live search-->
           <!--attendance list-->
        </div>
        <div class="inner_card_bd" v-if="entry==2">
            <!--score sheet--> 
            <br/>
            <div class="row form-group">
                <div class="col-12" style="display:flex;flex-direction:row">
                    <div class="col-8"></div>
                    <div class="col-2">
                        <button type="button" class="form-control button" @click="add_cat()" style="color:green"><b>Add</b></button>
                    </div>
                    <div class="col-2">
                        <button type="button" class="form-control button" @click="remove_cat()" style="color:red"><b>Remove</b></button>
                    </div>
                </div>
            </div><br/><br/>
            <div class="row form-group">
                <div class="col-12" style="display:flex;flex-direction:row">
                    <div class="col-8" style="padding-left:0">
                        <label>Enter Score Category</label>
                        <input type="text" class="form-control" v-model="score_cat[0]" placeholder="Enter Category Description"/>
                    </div>
                    <div class="col-4">
                        <label>Enter Percentage (%)</label>
                        <input type="number" step="0.01" min='1' class="form-control" v-model="score_percentage[0]"/>
                    </div>
                </div>
            </div>
            <div v-for="l in counter_cat" class="row form-group">
                <div class="col-12" style="display:flex;flex-direction:row">
                    <div class="col-8" style="padding-left:0">
                        <input type="text" class="form-control" v-model="score_cat[l]" placeholder="Enter Category Description"/>
                    </div>
                    <div class="col-4">
                        <input type="number" step="0.01" min='1' class="form-control" v-model="score_percentage[l]"/>
                    </div>
                </div>
            </div>
            <!--score sheet-->  
        </div>
    </div>

        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm" v-if="form">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="button" class="btn btn-danger btn-sm" @click="previous" v-if="isTWQA">
              <i class="fa fa-undo"></i> Return
            </button>
        </div>
    </form>
    <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
        <v-data-table
            :headers="headers"
            :items="user_data"
            :search="search"
            class="elevation-1"
            :rows-per-page-items="[10]"
            v-if="hv_user"
            >
        <template v-slot:items="props">
            <tr v-on:click="select_user(props.item)" style='cursor:pointer'>
                <td><a href="" :id= props.item.username v-on:click.prevent="select_user(props.item.name)">Select</a></td>
                <td>{{ props.item.name }}</td>
            </tr>
        <template v-slot:pageText="props">
            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
        </template>
        
        </template>
        </v-data-table>  
    </div>
</div>

</template>


<script>
import axios from 'axios';
export default {
    data(){          
        return{
            user_type:['teacher','teacher'],
            pta_list:[],
            imc_list:[],
            cat_list:[],
            principal_p:'',
            principal_k:'',
            fetch_list:[],
            isSET:false,
            counter_cat:2,
            score_cat:[],
            score_percentage:[],
            display_tag:'',
            isTWQA:false,
            tender_list:[],
            isSSB:false,
            ready:false,
            ssb:'',
            teacher:[],
            counter_teacher:1,
            principal:'CHAN YIN HUNG KUBY',
            imc:'',
            imc_ssb:'',
            pta:'',
            sgm:'',
            sgm_list:[],
            form:false,
            header_text:'Tender Meeting Setup',
            proc_id:'',
            entry:0, 
            user_data:[],
            hv_user:false,
            teacher_selected:[],
            headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'name',sortable: false},
            ], 
            user_index:0,
        }  
    },
async created(){
    await axios.get('Procurement/shared_backend/get_principal.php')
        .then(
            (response)=>{
                this.principal = response.data[0].name;
            }
        )
    this.proc_id = document.getElementById('tmp_id').value;
    var formData = new FormData();
    formData.append('proc_id',this.proc_id);
    await axios.post('Procurement/procurement/backend/attendence_list_fetch.php',formData)
        .then(
            (response)=>{
                var id=0;
                if(!(response.data=='empty')){
                    this.fetch_list = response.data;
                    this.imc = this.fetch_list[id].name; //0    
                    this.principal = this.fetch_list[id+1].name; //2
                    if(this.fetch_list[id+2].rank=='Principal(P)'){                       
                        this.principal_p = this.fetch_list[id+2].name;  //2(ssb)
                        this.principal_k = this.fetch_list[id+3].name;  //3(ssb)
                        id = id+1;
                    } else {
                        this.pta = this.fetch_list[id+2].name;  //2(non ssb)
                    }                                       
                    this.sgm = this.fetch_list[id+3].username;  //ssb:4 non-ssb:3
                    var i = id+4;
                    this.counter_teacher = this.counter_teacher-2;
                    var y = 0;
                    for(i=(id+4);i<this.fetch_list.length;i++){
                        this.user_type[y] = this.fetch_list[i].rank;
                        this.teacher_selected[y] = this.fetch_list[i].username;
                        this.teacher[y] = this.fetch_list[i].name;
                        this.counter_teacher++;
                        y++;
                        
                    }
                }
            }
        )
    await axios.post('Procurement/procurement/backend/fetch_score_sheet.php',formData)
        .then(
            (response)=>{
                if(!(response.data=='Empty')){
                    this.cat_list = response.data;
                    var i = 0;
                    this.counter_cat = this.cat_list.length-1;
                    for(i=0;i<this.cat_list.length;i++){
                        this.score_cat[i] = this.cat_list[i].category;
                        this.score_percentage[i] = this.cat_list[i].percentage;
                    }
                }
            }
        )
    await axios.get('Procurement/procurement/backend/isTWQA.php')
        .then(
            (response)=>{
                if(response.data=='exist'){
                    this.entry = 0;
                    this.isTWQA = true;
                } else {
                    this.entry = 2;
                    this.isTWQA = false;
                    this.form = true;
                }
            }
        )   
    await axios.post('Procurement/procurement/backend/tender_meeting_isssb.php',formData)
        .then(
            (response)=>{
                this.tender_list = response.data;
                this.ssb = this.tender_list[0].ssb;
                this.display_tag = this.tender_list[0].display_tag;
                if(this.ssb=='yes'){
                    this.isSSB = true;
                } else {
                    this.isSSB = false;
                }
            }
        )
    await axios.get('Procurement/procurement/backend/fetch_smg_list.php')
        .then(
            (response)=>{
                this.sgm_list = response.data;
            }
        )
    await axios.get('Procurement/procurement/backend/fetch_imc_list.php')
        .then(
            (response)=>{
                this.imc_list = response.data;
            }
        )
    await axios.get('Procurement/procurement/backend/fetch_pta_list.php')
        .then(
            (response)=>{
                this.pta_list = response.data;
            }
        )
        this.ready = true;
},
    methods:{
        search_user(n){
            this.teacher_selected[n]='';
            var formData = new FormData();
            formData.append('key', this.teacher[n]);
            axios.post('RequestPortal/backend/search_user_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_user=false; 
                    } else {
                        this.user_index = n;
                        this.user_data = response.data;
                        this.hv_user=true;
                    }
                }); 
        },
        select_user(event){
            var i = this.user_index;
            this.teacher[i] = event.name;
            this.teacher_selected[i] = event.username;
            this.hv_user = false;
            this.$forceUpdate();
        },
        select_cat(cat){           
            if(cat == 1){
                this.header_text = 'Tender Declaration Setup';
                this.form = true;
                this.entry = cat;
            } else if(cat == 2){
                this.header_text = 'Score Sheet Setup';
                this.form = true;
                this.entry = cat;
            }
        },
        previous(){
            if(this.entry>0){
                this.header_text = 'Tender Meeting Setup';
                this.form = false;
                this.entry=0;
            } else {
                this.$router.go(-1);
            }
        },
        add(){         
            var i = this.counter_teacher+1;
            if((this.user_type[i]==null)||(this.user_type[i]==undefined)||(this.user_type[i]=='')){
                this.user_type[i] = "teacher";
            }        
            this.counter_teacher++;
        },
        remove(){
           this.counter_teacher--; 
        },
        add_cat(){
            if(this.counter_cat<4){
                this.counter_cat++;
            }
        },
        remove_cat(){
            if(this.counter_cat>2){
                this.counter_cat--;
            }
        },
        handleSubmit(){
            var readysubmit = true;
            var notready = 0;
            if(this.entry==1){
                //attendence list
                var formData = new FormData();
                formData.append('proc_id',this.proc_id);
                formData.append('manager',this.imc);
                formData.append('ssb',this.isSSB);
                if(this.isSSB == true){
                    formData.append('principal_p',this.principal_p);
                    formData.append('principal_k',this.principal_k);
                } else {
                    formData.append('pta',this.pta);    
                }
                formData.append('principal',this.principal);
                formData.append('sgm',this.sgm);                              
                formData.append('counter',this.counter_teacher);
                var i = 0;
                for(i=0;i<this.counter_teacher+1;i++){
                    if(this.teacher_selected[i]==''){
                        readysubmit = false;
                        notready = i;
                    }
                    formData.append('usertype_'+i,this.user_type[i]);
                    formData.append('teacher_'+i,this.teacher_selected[i]);
                }
                if(readysubmit==true){
                    var confirm_msg = "Confirm Submitting Declaration Form Name List?";
                    this.$confirm(confirm_msg).then(
                        ()=>{
                            axios.post('Procurement/procurement/backend/attendence_setup.php',formData)
                            .then(
                                (response)=>{
                                    if(response.data=='OK'){
                                        this.$alert('Successfully Submitted, Return to previous page');
                                        this.entry=0;
                                    } else {
                                        this.$alert(response.data);
                                    }
                                }
                            )
                        }
                    )
                } else {
                    this.$alert('Please select participant for teacher/staff:'+(i+1));
                }
                //attendence list
            } else if(this.entry==2){
                var formData = new FormData();
                var i = 0 ;
                var check_p = 0;
                formData.append('proc_id',this.proc_id);
                formData.append('counter',this.counter_cat);
                for(i=0;i<this.counter_cat+1;i++){
                    formData.append('cat_'+i,this.score_cat[i]);
                    formData.append('percent_'+i,this.score_percentage[i]);
                    check_p = check_p + Number(this.score_percentage[i]);
                }
                if(check_p==100){
                    axios.post('Procurement/procurement/backend/insert_score_sheet.php',formData)
                        .then(
                            (response)=>{
                                if(response.data=='OK'){
                                    this.$alert('Successfully Submitted, Return to previous page');
                                    if(this.isTWQA){
                                        this.entry=0;
                                    } else {
                                        this.$router.push('view_proc');
                                    }                                
                                } else {
                                    this.$alert(response.data);
                                }
                            }
                        )  
                } else {
                    this.$alert('Please Ensure Total Percentage Equals to 100');
                }
            }
        }
    }
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
</style>


